@import '../../../../assets/sass/breakpoints';
@import '../../../../assets/sass/mixins';

.usercard-container {
	@include wideCard;

	@include md {
		padding: 1.5rem 2.5rem;
	}

	@include custom(400) {
		padding: 1rem 2rem;
	}

	&__img {
		// @include circleImg;
		margin-right: 3rem;

		height: 10rem;

		@include md {
			height: 8rem;
		}

		@include sm {
			height: 7rem;
		}
	}

	&__user-info {
		&__name {
			font-size: 3rem;
			@include md {
				font-size: 2.5rem;
			}

			@include sm {
				font-size: 2rem;
			}
		}

		&__title,
		&__location {
			font-size: 1.55rem;

			@include sm {
				font-size: 1.25rem;
			}
		}

		.btn--highlight {
			margin-top: 1rem;
		}
	}

	&__skills {
		margin-left: auto;
		font-size: 1.75rem;
		color: rgb(68, 68, 68);
		font-weight: 300;

		@include sm {
			font-size: 1.25rem;
		}

		@include custom(400) {
			display: none;
		}

		&__skill {
			display: flex;
			align-items: center;

			span {
				color: var(--color-primary);
				font-size: 1rem;
				margin: 0.4rem 0.5rem 0 0;
				font-weight: 500;
			}
		}
	}
}
